<script>
// import i18n from '@/i18n'

var hours = []
var testData = {}
for (var i = 1; i < 25; i++) {
  hours.push({
    hr: i,
    start: false,
    quarter: false,
    half: false,
    quarter_til: false,
    event_name: '',
    event_color: '',
    label_quarter: false,
    label_hour: 0
  })
}

testData.hours = hours

export default {
  name: 'calendar',

  data: () => ({
    start: null,
    end: null,
    focus: new Date().toISOString().substring(0, 10),
    year_month: new Date().toISOString().substring(0, 7),
    events: [],
    days: {},
    active_types: ['type1', 'type2', 'type3', 'type4'],
    event_colors: { type1: 'cyan', type2: 'green', type3: 'orange', type4: 'red' },
    names: ['Holiday', 'PTO', 'Travel', 'Event', 'Birthday', 'Conference', 'Party'],
    upcomingEvents: [],
    wkStartDates: [],
    hours: testData.hours,
    intervals: [],
    showPicker: false,
    day_start: 7
  }),

  computed: {
    title () {
      const { start, end } = this
      if (!start || !end) return ''

      const startMonth = this.monthFormatter(start)
      // const endMonth = this.monthFormatter(end)
      // const suffixMonth = startMonth === endMonth ? '' : endMonth

      const startYear = start.year
      // const endYear = ', ' + end.year
      // const suffixYear = startYear === endYear ? '' : endYear
      // const dayOfWeek = i18n.t('calendar.daysOfWeek')[start.weekday]
      return `${startMonth} ${startYear}`
      // switch (this.$route.params.view) {
      //   case 'month':
      //     return `${startMonth} ${startYear}`
      //   case 'week':
      //     if (suffixMonth.length && suffixYear.length) {
      //       return `${startMonth} ${start.day} ${startYear} - ${suffixMonth} ${end.day} ${suffixYear}`
      //     } else if (suffixMonth.length) {
      //       return `${startMonth} ${start.day} - ${suffixMonth} ${end.day}${startYear}`
      //     } else {
      //       return `${startMonth} ${start.day} - ${end.day}${startYear}`
      //     }
      //   case '4day':
      //     return `${startMonth} ${start.day}${startYear} - ${suffixMonth} ${end.day}${suffixYear}`
      //   case 'day':
      //     // return `${dayOfWeek} ${startMonth} ${start.day}${startYear}`
      //     return `${dayOfWeek} ${start.day} ${startMonth}${startYear}`
      // }
      // return ''
    },
    monthFormatter () {
      return this.$refs.calendar.getFormatter({
        timeZone: 'UTC', month: 'long'
      })
    },
    focusDateLong () {
      return new Date(this.focus).toLocaleDateString('en-US', { year: 'numeric', month: 'long', day: 'numeric' })
    },
    focusMonth () {
      const date = this.formatDate(new Date(), true)
      return date.toString()
    }
  },

  methods: {
    setToday () {
      this.focus = this.today
    },
    prev () {
      this.$refs.calendar.prev()
    },
    next () {
      this.$refs.calendar.next()
    },
    nth (d) {
      return d > 3 && d < 21 ? 'th' : ['th', 'st', 'nd', 'rd', 'th', 'th', 'th', 'th', 'th', 'th'][d % 10]
    },

    updateRange ({ start, end }) {
      this.days = []
      this.wkStartDates = []
      const events = []
      const min = new Date(`${start.date}T00:00:00`)
      const max = new Date(`${end.date}T23:59:59`)
      const days = (max.getTime() - min.getTime()) / 86400000
      const eventCount = this.rnd(days, days + 20)

      for (let i = 0; i < eventCount; i++) {
        const allDay = this.rnd(0, 3) === 0
        const firstTimestamp = this.rnd(min.getTime(), max.getTime())
        const first = new Date(firstTimestamp - (firstTimestamp % 900000))
        const secondTimestamp = this.rnd(2, allDay ? 288 : 8) * 900000
        const second = new Date(first.getTime() + secondTimestamp)

        events.push({
          name: this.names[this.rnd(0, this.names.length - 1)],
          start: this.formatDate(first, !allDay),
          end: this.formatDate(second, !allDay),
          type: Object.keys(this.event_colors)[this.rnd(0, 3)],
          allDay: allDay
        })
      }

      this.start = start
      this.end = end
      this.events = events
      const dateRange = this.getDatesInRange(new Date(start.date), new Date(end.date))
      for (var date of dateRange) {
        this.days[date] = []
      }

      for (var e of events) {
        const startDate = new Date(e.start.split(' ')[0])
        const endDate = new Date(e.end.split(' ')[0])
        // if (startDate !== endDate) {
        const eventDates = this.getDatesInRange(startDate, endDate)
        for (var edate of eventDates) {
          if (edate in this.days) {
            this.days[edate].push(e)
          }
        }
      }
      const today = new Date()
      var maxUpcoming = 3
      for (var dt in this.days) {
        if (Date.parse(dt) > today) {
          for (var ev of this.days[dt]) {
            if (this.upcomingEvents.length < maxUpcoming) {
              this.upcomingEvents.push(ev)
            }
          }
        }
      }
    },
    rnd (a, b) {
      return Math.floor((b - a + 1) * Math.random()) + a
    },
    getEventColour (event) {
      return event.color
    },
    getDatesInRange (start, end) {
      var arr = []
      var rangeDone = false
      while (!rangeDone) {
        var dt = start
        dt.setDate(dt.getDate() + 1)
        arr.push(this.formatDate(new Date(dt), true).split(' ')[0])
        if (dt >= end) {
          rangeDone = true
        }
      }
      return arr
    },
    formatDate (a, withTime) {
      return withTime
      // Leading 0 needed for months/dates when comparing with calendar dates
        ? `${a.getFullYear()}-${('0' + (a.getMonth() + 1)).slice(-2)}-${('0' + (a.getDate())).slice(-2)} ${a.getHours()}:${a.getMinutes()}`
        : `${a.getFullYear()}-${('0' + (a.getMonth() + 1)).slice(-2)}-${('0' + (a.getDate())).slice(-2)}`
    },
    eventColorsToDisplay (date) {
      const colors = []
      if (!(date in this.days)) {
        return []
      }
      const dayEvents = this.days[date]
      for (var e of dayEvents) {
        if (!e.allDay) {
          if (!colors.includes(this.event_colors[e.type]) && colors.length < 4) {
            colors.push(this.event_colors[e.type])
          }
        }
      }
      const rows = []
      const row1 = []
      const row2 = []
      for (var i = 0; i < colors.length; i++) {
        var color = colors[i]
        if (i < 2) {
          row1.push(color)
        } else {
          row2.push(color)
        }
      }
      if (row2.length) { rows.push(row2) }
      if (row1.length) { rows.push(row1) }
      return rows
    },
    getAllDayEvent (date) {
      const allDayEvent = []
      if (!(date in this.days)) {
        return []
      }
      const dayEvents = this.days[date]
      for (var e of dayEvents) {
        if (e.allDay) {
          allDayEvent.push(e)
          return allDayEvent
        }
      }
    },
    setWeekStart (week) {
      const dt = week[0].date
      if (!this.wkStartDates.includes(dt)) {
        this.wkStartDates.push(dt)
      }
      return dt
    },
    toggleEventTypeActive (type) {
      if (this.active_types.includes(type)) {
        var index = this.active_types.indexOf(type)
        this.active_types.splice(index, 1)
      } else {
        this.active_types.push(type)
      }
    },
    // buildSelectedDayEvents (date) {
    //   if (!(date in this.days)) {
    //     this.selectedDayEvents = []
    //   } else {
    //     const formattedEvents = []
    //     for (ein of this.days[date]) {
    //       if (!ein.allDay) {
    //         const dt = new Date(date)
    //         let eout = {}
    //         eout.name = ein.name
    //         eout.hours
    //       }
    //     }
    //   }
    // }
    roundToQuarterHour (time) {
      const timeToReturn = new Date(time)
      timeToReturn.setMinutes(Math.round(timeToReturn.getMinutes() / 15) * 15)
      return { hour: timeToReturn.getHours(), minutes: timeToReturn.getMinutes() }
    },
    getEventMidpoint (start, end) {
      const diff = end - start
      const minsToMid = Math.floor(((diff / 1000) / 60) / 2)
      const midpoint = new Date(start)
      midpoint.setMinutes(start.getMinutes() + minsToMid)
      const roundedTime = this.roundToQuarterHour(midpoint)
      midpoint.setHours(roundedTime.hour)
      midpoint.setMinutes(roundedTime.minutes)
      // return this.formatDate(midpoint, true)
      return midpoint
    },
    buildIntervals (events) {
      for (var e of events) {
        if (!e.allDay) {
          const startDate = e.start.split(' ')[0]
          const endDate = e.end.split(' ')[0]
          const startHour = parseInt(e.start.split(' ')[1].split(':')[0])
          const startMinute = parseInt(e.start.split(' ')[1].split(':')[1])
          const endHour = parseInt(e.end.split(' ')[1].split(':')[0])
          const endMinute = parseInt(e.end.split(' ')[1].split(':')[1])

          var eventHours = []
          for (var i = 0; i < (endHour - startHour); i++) {
            eventHours.push(startHour + i)
          }
          if (endMinute > 0) { eventHours.push(endHour) }
          let labelQuarter = 'start'
          const midpointdt = this.getEventMidpoint(new Date(e.start), new Date(e.end))
          const min = midpointdt.getMinutes()
          if (min === 0) { labelQuarter = 'start' }
          if (min === 15) { labelQuarter = 'quarter' }
          if (min === 30) { labelQuarter = 'half' }
          if (min === 45) { labelQuarter = 'quarter_til' }
          for (var eh of eventHours) {
            const interval = {
              type: e.type,
              name: e.name,
              label_hour: midpointdt.getHours(),
              label_quarter: labelQuarter,
              quarters: []
            }
            if (eh === startHour) {
              if (startMinute === 0) {
                interval.quarters.push('start')
              }
              if (startMinute <= 15) {
                interval.quarters.push('quarter')
              }
              if (startMinute <= 30) {
                interval.quarters.push('half')
              }
              if (startMinute <= 45) {
                interval.quarters.push('quarter_til')
              }
              this.intervals[`${startDate}-${eh}`] = interval
            } else if (eh === endHour) {
              if (endMinute > 0) {
                interval.quarters.push('start')
              }
              if (endMinute > 15) {
                interval.quarters.push('quarter')
              }
              if (endMinute > 30) {
                interval.quarters.push('half')
              }
              if (endMinute > 45) {
                interval.quarters.push('quarter_til')
              }
              this.intervals[`${endDate}-${eh}`] = interval
            } else {
              interval.quarters = ['start', 'quarter', 'half', 'quarter_til']
              this.intervals[`${startDate}-${eh}`] = interval
            }
          }
        }
      }
    },
    getInterval (date, hour) {
      var key = `${date}-${hour}`
      if (!(key in this.intervals)) {
        return [{
          type: null,
          name: null,
          label_hour: 0,
          label_quarter: null,
          quarters: []
        }]
      } else {
        const returnArr = []
        returnArr.push(this.intervals[key])
        return returnArr
      }
    },
    quarterStyle (interval, hour, quarter) {
      const styles = ['quarter-hour']
      if (!((interval.label_hour === hour) && (interval.label_quarter === quarter))) {
        styles.push('transparent-text')
      }
      return styles
    },
    setFocus ($event) {
      this.focus = $event.date
    },
    updateCalendar () {
      this.focus = `${this.year_month}-${this.focus.slice(-2)}`
      this.showPicker = false
    }
  },
  mounted () {
    this.buildIntervals(this.days[this.focus])
  }
}
</script>
